<template>
  <div class="withdrawal-requests">
      <button class="back-button" @click="goBack">
      <img class="back-button-arrow" src="@/assets/newImages/black_arrow.png"/>
      <template  v-if="from=='bonusAccount'">
        <p class="back-button-text" v-if="coinTicker">{{$localizeFilter('bonus_account') + ' ' + coinTicker}}</p>
        <p class="back-button-text" v-else>{{$localizeFilter('bonus_account')}}</p>
      </template>
      <template v-else>
         <template v-if="coinTicker">
            <p class="back-button-text">{{$localizeFilter('wallet') + ' ' + coinTicker}}</p>
            <img :src="require('@/assets/newImages/coins/icons/' + coinTicker.toLowerCase() + '_icon.png')" class="coin-icon">
          </template>
          <p class="back-button-text" v-else>{{$localizeFilter('wallet')}}</p>
      </template>
      </button>
      <p class="title">{{$localizeFilter('withdrawal_applications')}}</p>
      <WithdrawalRequestsTable
      :showTable="requests.length > 0"
      :requests="requests" @changeStatus="changeStatus" @cancelRequest="cancelRequest"/>
      <EmptyTable v-if="requests.length == 0" :text="$localizeFilter('no_withdrawal_requests')"/>
      <pagination v-model="page" :records="count" :per-page="pageSize" @paginate="pageChangeHandler"/>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import WithdrawalRequestsTable from '@/components/WithdrawalRequestsTable'
import paginationMixin from '@/mixins/pagination.mixin'
import EmptyTable from "@/components/EmptyTable"

export default defineComponent({
  props: {
    from: { // wallet, bonusAccount
      type: String,
      required: true
    },
    coinTicker: {
      type: String,
      default: null
    }
  },
  components: {
    WithdrawalRequestsTable, EmptyTable
  },
  mixins: [paginationMixin],
  watch:{
      page() {
          this.$nextTick(this.loadData);
      },
  },
  setup() {
    
  },
  data: () => ({
    requests: [],
    count: 0,
    status: 'all'
  }),
  mounted() {
      this.loadData()
  },
  methods: {
     goBack() {
        this.$router.back()
     },
     cancelRequest(idx) {
       this.requests[idx].status = 'canceled'
       this.$forceUpdate()
     },
     changeStatus(status) {
       this.status = status
       this.loadData()
     },
     async loadData() {
         try {
          const data = {status: this.status, skip: this.skip, limit: this.limit, currencyCode: this.coinTicker || 'GDC', type: this.from}
            this.requests = await this.$store.dispatch('getWithdrawBalanceRequests', data)
            const {count} = await this.$store.dispatch('getWithdrawBalanceRequestsCount', data)
            this.count = count
            this.setupPagination(this.requests)
        } catch(e) {
            this.$error(e)
        } 
     }
  }
})
</script>

