<template>
<div class="place-for-table">
  <div class="table-options-block">
      <TableOptions class="table-options" :name="$localizeFilter('applications')" :options="statuses" :prefix="'status_'" @selectOption='selectOption'/>
  </div>
  <div class="custom-table desktop" v-if="showTable">
    <div class="custom-table-header">
      <div class="custom-table-row light header">
        <div class="custom-data-cell header">{{$localizeFilter('application_date')}}</div>
        <div class="custom-data-cell header" style="width: 20%;">{{$localizeFilter('application_amount') + ' ' + (requests[0].coin)}}</div>
        <div class="custom-data-cell header" style="width: 20%;">{{$localizeFilter('status')}}</div>
        <div class="custom-data-cell header" style="width: 35%;">{{$localizeFilter('address')}}</div>
        <!-- <div class="custom-data-cell header">{{$localizeFilter('action')}}</div> -->
    </div>
    </div>

    <div class="custom-table-body">
        <div class="custom-table-row" v-for="(request, idx) in requests"  :key="idx" :class="idx % 2 == 0 ? 'dark': 'light'">
            <div class="custom-data-cell">
              <p> {{`${$dateTimeFilter(request.createdTimestamp, 'custom', null, {day: '2-digit', month: 'long', year: 'numeric'})},\n${$dateTimeFilter(request.createdTimestamp, 'custom', null, {hour: '2-digit',  minute: '2-digit'})}`}}</p>
            </div>
            <div class="custom-data-cell" style="width: 20%;">
                <p class="request-amount">
                 <span style="font-weight:600;">{{ displayCurrency(request.amount) + ' ' + (request.coin)}}</span>
                 <br>
                 <span style="font-size: 13px;">{{$localizeFilter('commission') + ' ' + displayCurrency(request.commission) + ' ' + (request.coin)}}</span>
                </p>
            </div>
            <div class="custom-data-cell status" :class="request.status" style="width: 20%;">{{ $localizeFilter('status_' + request.status) }}</div>

            <div class="custom-data-cell" style="width: 35%;">
                <p class="request-amount">
                 {{ request.withdrawMethod == 'GDC.TRC20' ? 'TRON' : request.withdrawMethod }}
                 <br>
                 <span style="font-size: 13px;">{{ request.withdrawWalletAddress }}</span>
                </p>
            </div>

            <!-- <div class="custom-data-cell"><button class="basic-button" v-if="request.status=='pending'" @click="cancelRequest(request, idx)">{{ $localizeFilter('cancel') }}</button></div> -->
        </div>
    </div>
  </div>

    <div class="custom-table mobile" v-if="showTable">
    <div class="custom-table-body">
        <div class="custom-table-row" v-for="(request, idx) in requests"  :key="idx" :class="idx % 2 == 0 ? 'dark': 'light'">
            <div class="custom-data-cell">
               <p> {{`${$dateTimeFilter(request.createdTimestamp, 'custom', null, {day: '2-digit', month: 'long', year: 'numeric'})},\n${$dateTimeFilter(request.createdTimestamp, 'custom', null, {hour: '2-digit',  minute: '2-digit'})}`}}</p>
            </div>
            <div class="custom-data-cell">
                <p class="request-amount">
                 <span style="font-weight:600;">{{ displayCurrency(request.amount) + ' ' + (request.coin)}}</span>
                 <br>
                 <span style="font-size: 13px;">{{$localizeFilter('commission') + ' ' + displayCurrency(request.commission) + ' ' + (request.coin)}}</span>
                </p>
            </div>
            <div class="custom-data-cell status" :class="request.status">{{ $localizeFilter('status_' + request.status) }}</div>
            <div class="custom-data-cell">
                <p class="request-amount">
                 {{ request.withdrawMethod == 'GDC.TRC20' ? 'TRON' : request.withdrawMethod }}
                 <br>
                 <span style="font-size: 13px;">{{ request.withdrawWalletAddress }}</span>
                </p>
            </div>
             <!-- <div class="custom-data-cell header" v-if="request.status=='pending'">{{ ' ' }}</div> -->
            <!-- <div class="custom-data-cell" v-if="request.status=='pending'"><button class="basic-button" @click="cancelRequest(request, idx) ">{{ $localizeFilter('cancel') }}</button></div> -->
        </div>
    </div>
  </div>
  </div>
</template>

<script>
import TableOptions from "@/components/TableOptions.vue"
import localizeFilter from '@/filters/localize.filter'
import utils from '@/utils/utils'

export default {
  props: {
    requests: {
      type: Array,
      default: (() => {
          return []
      })
    },
    showTable: {
      type: Boolean,
      default: false
    }
  },
  //  watch: { 
  //       requests: function(newVal, oldVal) {
  //           // console.log('???',newVal)
  //       }
  //   },
  data: () => ({
    statuses: ['all','pending', 'completed','canceled'],
    status: 'all'
  }),
  components: {
    TableOptions
  },
  methods: {
    selectOption(idx) {
      this.status = this.statuses[idx]
      this.$emit('changeStatus',this.status)
    },
    async cancelRequest(request, idx) {
       try {
          await this.$store.dispatch('cancelWithdrawBalanceRequest', {requestId: request.id})
          this.$message(localizeFilter('application_calceled'))
          this.$emit('cancelRequest',idx)
        } catch (e) {
             this.$error(e)
        }
    },
    displayCurrency(value) {
      let coinTicker = this.requests[0].coin
      return +utils.displayBigIntAsDecimal(value, this.$store.getters.assetDecimalsMap[coinTicker], this.$store.getters.assetDecimalsMap[coinTicker].toString().length-1)
    },
  },
  computed: {
  },
}
</script>

